export default {
  props: {
    value: [String, Number, Array, Object],
    noday: Boolean
  },
  watch: {
    value (newValue) {
      this.currentValue = newValue
    },
    currentValue (val) {
      this.$emit('input', val)
    }
  },
  data () {
    return {
      currentValue: this.value
    }
  },
  methods: {
    handleOnClick (gra) {
      this.currentValue = gra
    }
  }
}



