data() {
return {
classId: null, //指标大类id
className: null, //指标大类名称
isummary: false, //是否汇总
isummapply: false, //指标维度汇总是否已经申请过
isdimapply: false, //维度是否已经申请过
indId: null, //指标id
indName: null, //指标名称
dimId: null,//维度ID
dimName: null,//维度名称
selIndItem: [], //已选择的指标数组
indItem: [],//指标列表
dimItem: [],//维度列表
hostCityItems: [],//热门城市
otherCityItems: [],//其他城市
isDimDisabld: false,
isDimListDisabld: false,
isNoSelInd: false,
isSelDim: false,
selDimName: null,
secIndName: null,
isCity: false,
hostCityItem: null,
