
import Trend from './Trend'
import EditInd from './EditInd'
import Cookie from 'js-cookie'
import Scroller from 'vux/src/components/scroller/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'


const list = () => ['日指标', '周指标','月指标']

export default {
components: {
Scroller,
LoadMore,
ViewBox,
Spinner,
XTable,
ButtonTab,
EditInd,
ButtonTabItem
},
data () {
return {
dataShow: false,
uid: 0,
uname: "",
listdata: [],
tag: '日指标',
taglist: list(),
startDate: '',
lupdate: '',
index: 0,
indexs: 0,
page: 1,
dataType: "D",
scrollTop: 0,
onFetching: false,
bottomCount: 15,
size: 150
}
},
mounted : function(){
this.getList();

},
methods: {
editInd(){

this.$router.push({path: '/bdc-prd-dbd/editInd'})
},
sendMsgToParent:function(indName,index){
this.$emit("listenToPreiew",indName,index,1);
},
getList(item,index){
this.tag = item;
this.index = index;
this.uid = Cookie.get('t8t-it-uid');
this.$http.fetch('acc/employee/findById',
{
id: this.uid
})
.then((response) => {
if(response.data.status == 200){
Cookie.set('t8t-oa-username', response.data.result.name+","+response.data.result.enName)
}
}, (response) => {
});
this.uname = Cookie.get('t8t-oa-username');
this.$http.fetch('acc/employee/findSubId',
{
"uid":this.uid,
"subNum":1,
"type":1
})
.then((response) => {
if(response.data.status == 200){
Cookie.set('t8t-oa-item', response.data.result.name+","+response.data.result.enName)
}
}, (response) => {
});




this.dataType = "D";
if (this.index == 0) {
this.dataType = "D"
}else if(this.index == 1){
this.dataType = "W"
}else if(this.index == 2){
this.dataType = "M"
}
this.$http.fetch('dsa/dataBoard/indPvw/pageList',
{
uid: this.uid,
uname: this.uname,
dataType: this.dataType,
page: this.page,
size: this.size
})
.then((response) => {
this.dataShow = false;
this.startDate = ''
this.lupdate = ''
if(response.data.status == 200){
if(response.data.result.total > 0){
this.startDate = response.data.result.endate
this.lupdate = '更新时间：'+response.data.result.lupdate
let arrs = response.data.result.rows;
arrs = arrs.map((item) => {
let arr = new Array();
let xiaoshu = "";  
let zhengshu ="";  
let t = item.statis_num.toString();

if(item.statis_num < 1000 || t.indexOf('%') > 0){    //当参数小于1000的时候直接返回参数
return item;
}else{
;   
if(t.indexOf('.') > 0 ){   //如果参数存在小数，则记录小数部分与整数部分
var index = t.indexOf('.');
xiaoshu = t.slice(index,t.length);
zhengshu = t.slice(0,index);
}else{   //否则整数部分的值就等于参数的字符类型
zhengshu = t;
}
var num = parseInt(zhengshu.length/3);   


var head = zhengshu.slice(0,zhengshu.length-num*3);
if(head.length>0 && t  != '-'){  //如果head存在，则在head后面加个千位分隔符，
head += ',';
}
var body = zhengshu.slice(zhengshu.length-num*3,zhengshu.length);


var arr2 = new Array();
for(var i=0;i<num;i++){
arr2.push(body.slice(i*3,i*3+3));
}
body = arr2.join(',');   

zhengshu = head + body;  
item.statis_num = zhengshu + xiaoshu;   
return item;   

}

});
this.listdata = arrs;

this.dataShow = true;

}

}
}, (response) => {
});
},
onScrollBottom () {
if (this.onFetching) {

} else {//分页
this.onFetching = true


setTimeout(() => {




this.onFetching = false
}, 2000)
}
},
onScroll (pos) {
this.scrollTop = pos.top
},
changeList () {





},
}
};



