
import F2 from "@antv/f2";
import Cookie from "js-cookie";
import Scroller from 'vux/src/components/scroller/index.vue'
import Spinner from 'vux/src/components/spinner/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'
import ButtonTab from 'vux/src/components/button-tab/button-tab.vue'
import ButtonTabItem from 'vux/src/components/button-tab/button-tab-item.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import PopupPicker from 'vux/src/components/popup-picker/index.vue'
import Group from 'vux/src/components/group/index.vue'
import Cell from 'vux/src/components/cell/index.vue'
import Picker from 'vux/src/components/picker/index.vue'
import XButton from 'vux/src/components/x-button/index.vue'


const list = () => ["日指标", "周指标", "月指标"];
export default {
components: {
F2,
Cookie,
ButtonTab,
ButtonTabItem,
XTable,
PopupPicker,
Group,
Cell,
Picker,
XButton
},
data() {
return {
dataShow: false,
datav: [],
page:1,
size:100,
visualShow: false,
dateShow: false,
showList1: true,
scrollTop: 0,
onFetching: false,
bottomCount: 20,
listdata: [],
selistdate: [],
tag: '',
taglist: list(),
endate: '',
lupdate: '',
title: "趋势图",
dataType: "D",
uid:0,
uname:"",
list: [],
value: [""],
indName: '',
showPopupPicker: false,
isClick: false,
isSel: false,
selIndex: this.indextotrend,
stepIndName: '',
index: this.indextotrend,
};
},
mounted: function() {

this.getList();

this.pageList();

this.selist();


},
methods: {
onScrollBottom() {
if (this.onFetching) {

} else {
this.onFetching = true;
setTimeout(() => {
this.bottomCount += 10;
this.$nextTick(() => {
this.$refs.scrollerBottom.reset();
});
this.onFetching = false;
}, 2000);
}
},
getList(item, index) {
if(typeof(item) == "undefined"){
this.isClick = false;
if(this.inametotrend.length > 0){
this.index = this.indextotrend;
}else{
this.index = index;
}
}else{
this.isClick = true;
this.selIndex = index;
this.index = index;
}
this.tag = item;
this.uid = Cookie.get("t8t-it-uid");
this.uname = Cookie.get("t8t-oa-username");
this.dataType = "D";
this.stepIndName = this.indName;
if (this.index == 0) {
this.dataType = "D";
this.inametotrend = this.inametotrend.replace(/MAU/,"DAU");
this.inametotrend = this.inametotrend.replace(/WAU/,"DAU");
} else if (this.index == 1) {
this.dataType = "W";
this.inametotrend = this.inametotrend.replace(/MAU/,"WAU");
this.inametotrend = this.inametotrend.replace(/DAU/,"WAU");
} else if (this.index == 2) {
this.dataType = "M";
this.inametotrend = this.inametotrend.replace(/DAU/,"MAU");
this.inametotrend = this.inametotrend.replace(/WAU/,"MAU");
}

this.selist();
this.page = 1;
this.size = 100;
},
changeValue(){ //趋势图
this.$http.fetch('dsa/dataBoard/indTnd/numList',
{
uid:this.uid,
uname:this.uname,
dataType:this.dataType,
indName:this.indName
})
.then((response) => {
this.datav = []
this.visualShow = false;
if(response.data.status == 200){
if(response.data.result.rows.length > 0){
this.visualShow = true;

const canvas = document.getElementById("mountNode");
const ctx = canvas.getContext("2d");
const gradient = ctx.createLinearGradient(0, 0, window.innerWidth, 0);
gradient.addColorStop(0, "#06C792");
gradient.addColorStop(0.5, "#06C792");
gradient.addColorStop(1, "#06C792");

let maxDate = response.data.result.maxDate

let maxValues = response.data.result.maxValues


this.datav = response.data.result.rows

const chart = new F2.Chart({
id: "mountNode",
width: window.innerWidth,
height:
window.innerWidth > window.innerHeight
? window.innerHeight - 54
: window.innerWidth * 0.707,
pixelRatio: window.devicePixelRatio
});
const defs = {
日期: {
range: [0, 1],
tickCount: 5
},
值: {
tickCount: 5,
min: 0
}
};
chart.axis("日期", {
label(text, index, total) {
const cfg = {
textAlign: "center"
};
if (index === 0) {
cfg.textAlign = "start";
}
if (index > 0 && index === total - 1) {
cfg.textAlign = "end";
}
return cfg;
}
});
chart.source(this.datav, defs);

chart.tooltip({

showCrosshairs: true,
onShow(ev) {
const { items } = ev;
items[0].name = items[0].title;
}

});












chart
.line()
.position("日期*值")
.shape("smooth")
.color(gradient);
chart
.area()
.position("日期*值")
.shape("smooth")
.color(gradient);
chart.render();
}
}
}, (response) => {
});
},
pageList(){//分页列表
this.$http
.fetch("dsa/dataBoard/indTnd/pageList", {
uid: this.uid,
uname: this.uname,
dataType: this.dataType,
indName: this.indName,
page: this.page,
size: this.size
})
.then(
response => {
this.listdata = [];
if (response.data.status == 200) {
if(response.data.result.total > 0){
let arr = response.data.result.rows;
arr = arr.map((item) => {
let arr = new Array();
let xiaoshu = "";  
let zhengshu ="";  
let t = item.statis_num.toString();

if(item.statis_num < 1000 || t.indexOf('%') > 0){    //当参数小于1000的时候直接返回参数
return item;
}else{
if(t.indexOf('.')>0){   //如果参数存在小数，则记录小数部分与整数部分
var index = t.indexOf('.');
xiaoshu = t.slice(index,t.length);
zhengshu = t.slice(0,index);
}else{   //否则整数部分的值就等于参数的字符类型
zhengshu = t;
}
var num = parseInt(zhengshu.length/3);   


var head = zhengshu.slice(0,zhengshu.length-num*3);
if(head.length > 0 && t  != '-'){  //如果head存在，则在head后面加个千位分隔符，
head += ',';
}
var body = zhengshu.slice(zhengshu.length-num*3,zhengshu.length);


var arr2 = new Array();
for(var i=0;i<num;i++){
arr2.push(body.slice(i*3,i*3+3));
}
body = arr2.join(',');   

zhengshu = head + body;  
item.statis_num = zhengshu + xiaoshu;   
return item;   

}
});
this.listdata = arr;
}

}
},
response => {
}
);
},
selist(){//选择列
this.$http
.fetch("dsa/dataBoard/indTnd/seList", {
uid: this.uid,
uname: this.uname,
dataType: this.dataType
})
.then(
response => {
this.endate = '';
this.lupdate = '';
this.selistdate = [];
this.values = [];
this.list = [];
this.value = [];
this.indName = ''; 
this.value = [];
if (response.data.status == 200) {
this.endate = response.data.result.endate;
this.lupdate = '更新时间：'+response.data.result.lupdate;
this.isSel = false;
if(response.data.result.rows.length > 0){
this.selistdate = response.data.result.rows;

for (let arr of this.selistdate) {
this.values.push(arr.dim_ind_name);
}
this.list.push(this.values);

let  a = this.selistdate.shift();

if(!this.isClick && this.inametotrend.length > 0 && !this.isSel){
this.indName = this.inametotrend; 
this.value = [this.inametotrend];
this.index = this.indextotrend;
}else if(this.isClick && this.inametotrend.length > 0 && !this.isSel){
this.indName = this.inametotrend;
this.value = [this.inametotrend];
}else{
this.indName = a.dim_ind_name; 
this.value = [a.dim_ind_name];
}

this.changeValue();

this.pageList();

}
}
},
response => {
}
);
},

onChange(val) {

this.isSel = true;
this.index = this.selIndex;
this.indextotrend = this.index;
this.indName = val.join(",");


this.inametotrend = this.indName;

this.changeValue();
this.pageList();
},
showMsgFromChild(data) {

},
onShow() {

},
onHide() {

},
onScroll(pos) {

},
onCellClick() {

},
onClickButton() {

},
changeList() {






}
},
props: ['inametotrend','indextotrend']
};



