
import Cookie from 'js-cookie'
import ViewBox from 'vux/src/components/view-box/index.vue'
import Tab from 'vux/src/components/tab/tab.vue'
import TabItem from 'vux/src/components/tab/tab-item.vue'
import XButton from 'vux/src/components/x-button/index.vue'


import Preview from './Preview'
import Trend from './Trend'

const list = () => ['指标总览', '指标趋势']

export default {

components: {
Tab,
TabItem,
ViewBox,
XButton,

Preview,
Trend
},
data () {
return {
uid: null,//用户ID
uname: null,//用户姓名
token: null,
tag: '指标总览',
taglist: list(),
iname: '',
dataShow: 0,
index: 0,
tindex: 0,
}
},
ready () {
},
mounted : function(){
this.getIsShow();

},
methods:{
onClick(tag,item,index){
if(tag === '指标总览' && item==='指标总览'&&index===0){
this.iname = ''
this.tindex = 0
}
},
showMsgFromChild: function (iname,tindex,index){
this.iname = iname
this.index = index
this.tindex = tindex
},
getIsShow(){
this.uid = Cookie.get('t8t-it-uid');
this.uname = Cookie.get('t8t-oa-username')












let token = Cookie.get('t8t-it-token')
let ticket = Cookie.get('t8t-it-ticket')
this.$http.fetch('dsa/dataBoard/isApply',
{
uid: this.uid,
uname: this.uname,
token: token
})
.then((response) => {
if(response.data.status == 200){
if(response.data.result.isApply){
this.dataShow = 2
}else{
this.dataShow = 1
}
}
}, (response) => {
});
}
}
}



